@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Navbar {
  background: #222121;
  height: 3rem;
  padding: 1rem 0 0.05rem;
  position: relative;
  bottom: 1.5rem;
  width: 100vw;
}

a {
  color: #fff;
  font-family: "Oswald", sans-serif;
  text-decoration: none;
}

ul {
  padding: 0;
}

li {
  display: inline-block;
  list-style-type: none;
  margin-right: 2rem;
  text-decoration: none;
}

.title {
  background: #da8a8d;
  background-color: #da8a8d;
  background: conic-gradient(
    #da8a8d 25%,
    #f5f5f5 25%,
    #f5f5f5 50%,
    #77796c 50%,
    #77796c 75%,
    #7ee18d 75%,
    #7ee18d 100%
  );
  background-size: 100%;
  background-repeat: repeat;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-animation: titleHoverRev 0.5s ease forwards;
          animation: titleHoverRev 0.5s ease forwards;
  margin-left: 2rem;
  margin-top: 0;
  margin-bottom: 0;
}

.title:hover {
  animation: titleHover 1s forwards;
  -webkit-animation: titleHover 1s forwards;
  -moz-animation: titleHover 1s forwards;
  -o-animation: titleHover 1s forwards;
}

@-webkit-keyframes titleHover {
  0% {
    background-size: 100%;
    background-position: 0 0;
  }
  20% {
    background-size: 100%;
    background-position: 0 1em;
  }
  100% {
    background-size: 300%;
    background-position: -4em 1em;
  }
}

@keyframes titleHover {
  0% {
    background-size: 100%;
    background-position: 0 0;
  }
  20% {
    background-size: 100%;
    background-position: 0 1em;
  }
  100% {
    background-size: 300%;
    background-position: -4em 1em;
  }
}

@-webkit-keyframes titleHoverRev {
  0% {
    background-size: 300%;
    background-position: -4em 1em;
  }
  20% {
    background-size: 100%;
    background-position: 0 1em;
  }
  100% {
    background-size: 100%;
    background-position: 0 0;
  }
}

@keyframes titleHoverRev {
  0% {
    background-size: 300%;
    background-position: -4em 1em;
  }
  20% {
    background-size: 100%;
    background-position: 0 1em;
  }
  100% {
    background-size: 100%;
    background-position: 0 0;
  }
}

html {
  margin: 0;
  overflow: hidden;
  padding: 0;
}

.App {
  background-color: #222121;
}

.canvas > Canvas,
.canvas {
  height: calc(100vh - 3rem);
}

.canvasTest5 {
  background: rgb(253, 181, 45);
  background: linear-gradient(
    0deg,
    rgba(253, 181, 45, 1) 0%,
    rgba(176, 18, 90, 1) 100%
  );
}

